<template>
  <div class="button-wrapper">
    <div class="button-bg">
      <div class="button" :class="{active, wait}" @click="onClick">
        <span>{{title}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['title'],
  data () {
    return {
      active: false,
      wait: false
    }
  },
  methods: {
    onClick () {
      this.active = true;
      setTimeout(() => {
        this.active = false
        this.wait = true
        this.$emit('submit')
        setTimeout(() => {
          this.wait = false
        }, 450)
      }, 1000)
    }
  }
}
</script>

<style lang="less" scoped>
.button-wrapper {
  .button-bg {
    background: linear-gradient(90deg, #00ABFF, #CC27B7);
    border-radius: 2px;
    height: 34px;
    box-sizing: border-box;
    padding: 2px 2px 0;
  }
  .button {
    text-align: center;
    line-height: 30px;
    font-size: 16px;
    font-weight: 500;
    border-radius: 2px;
    background: #000028;
    position: relative;
    z-index: 3;
    overflow: hidden;
    span {
      background: linear-gradient(90deg, #00ABFF, #CC27B7);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      user-select: none;
    }
    &::before {
      content: '';
      width: 0;
      height: 400px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      background: linear-gradient(45deg, #00ABFF, #CC27B7);
      transition: 1s ease;
      display: block;
      z-index: -1;
    }
    &.active::before {
      width: 105%;
    }
    &.active, &.wait {
      span {
        background: transparent;
        color: #fff;
        -webkit-background-clip: border;
        -webkit-text-fill-color: #fff;
      }
    }
  }
}
</style>
