<template>
  <div class="button-wrapper">
    <span class="button-bg">
      <span class="button" :class="{active, wait}" @click="onClick">
        <span>{{title}}</span>
      </span>
    </span>
  </div>
</template>

<script>
export default {
  props: ['title'],
  data () {
    return {
      active: false,
      wait: false
    }
  },
  methods: {
    onClick () {
      this.active = true;
      setTimeout(() => {
        this.active = false
        this.wait = true
        this.$emit('submit')
        setTimeout(() => {
          this.wait = false
        }, 450)
      }, 1000)
    }
  }
}
</script>

<style lang="less" scoped>
.button-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  .button-bg {
    background: linear-gradient(90deg, #6DD9BB, #00ABFF);
    border-radius: 2px;
    width: 150px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .button {
    display: inline-block;
    text-align: center;
    width: 146px;
    line-height: 29px;
    font-size: 16px;
    font-weight: 500;
    border-radius: 2px;
    background: #000028;
    position: relative;
    z-index: 3;
    overflow: hidden;
    span {
      background: linear-gradient(90deg, #6DD9BB 0.12207031%, #00ABFF 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      user-select: none;
    }
    &::before {
      content: '';
      width: 0;
      height: 200px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      background: linear-gradient(45deg, #6DD9BB, #00ABFF);
      transition: 1s ease;
      display: block;
      z-index: -1;
    }
    &.active::before {
      width: 105%;
    }
    &.active, &.wait {
      span {
        background: transparent;
        color: #000A4F;
        -webkit-background-clip: border;
        -webkit-text-fill-color: #000A4F;
      }
    }
  }
}
</style>
