import store from "../store/index"
import { Toast } from 'vant';
import { i18n } from './i18n';
const web3Data = {
  web3: {},
  defaultAddress: '',
  t: i18n.global.t,
  Inval: null,
  initWeb3: async () => {
    if(window.tronWeb) {
      // try {
      //   await window.tronWeb.request({method: 'tron_requestAccounts'})
      //   console.log(11111)
      //   if(window.tronWeb.defaultAddress){
      //     console.log(window.tronWeb.defaultAddress.base58 ? window.tronWeb.defaultAddress.base58 : '')
      //    store.commit("updateAccount", window.tronWeb.defaultAddress.base58 ? window.tronWeb.defaultAddress.base58 : '');
      //    store.commit("updateProvider", window.tronWeb);
      //  }
      // } catch (error) {
     
      // }
      let i = 0;
      web3Data.Inval = setInterval(() => {
        i = i+1
        web3Data.defaultAddress = window.tronWeb.defaultAddress.base58 ? window.tronWeb.defaultAddress.base58 : ''
        if ((!window.tronWeb || !web3Data.defaultAddress) && i == 3) {
          Toast(web3Data.t('請連接錢包或登入錢包挿件'))
        }
        console.log(web3Data.defaultAddress, 'web3Data.defaultAddress...');
        if(web3Data.defaultAddress){
          window.clearInterval(web3Data.Inval);
          store.commit("updateProvider", window.tronWeb);
          store.commit("updateAccount", web3Data.defaultAddress);
          web3Data.getBlance(web3Data.defaultAddress)
        }
       }, 1000);
    } else {
      Toast(web3Data.t('請連接錢包或登入錢包挿件'))
    }
  },
  getBlance: (address) => {
    window.tronWeb.trx.getBalance(address).then((result) => {
      let balance = Number(result/Math.pow(10, 6)).toFixed(6)
      store.commit("updateCurrentBalance", balance);
      
    })
  }
}

export default web3Data;
