import {
  createRouter,
  createWebHashHistory
} from "vue-router";

const routes = [{
    path: '/',
    name: 'Index',
    component: () => import('@/views/index'),
    meta: {
      title: '',
      // keepAlive: true
    }
  },
  {
    path: '/net',
    name: 'Net',
    component: () => import('@/views/net'),
    meta: {
      title: ''
    }
  },
  {
    path: '/pledge/records',
    name: 'PledgeRecords',
    component: () => import('@/views/pledge/records'),
    meta: {
      title: ''
    }
  },
  {
    path: '/lp',
    name: 'Lp',
    component: () => import('@/views/lp'),
    meta: {
      title: ''
    }
  },
  {
    path: '/income',
    name: 'Income',
    component: () => import('@/views/income'),
    meta: {
      title: ''
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

router.beforeEach((to) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
})

export default router;
