import 'lib-flexible/flexible.js'
import {
  createApp
} from 'vue'
import App from './App.vue'
import ThemeButton1 from './components/ThemeButton1.vue'
import ThemeButton2 from './components/ThemeButton2.vue'
import router from "./router";
import store from "./store";
import Vant from 'vant';
import 'vant/lib/index.css';
import {
  intro,
  locale,
  region
} from "./storage"
import {
  i18n,
  setLang
} from "./utils/i18n"
import dateformat from "dateformat"
const lang = locale.get() || 'zh-TW'
setLang(lang)
// 返回上一页
const goBack = () => {
  if (history.length > 0) {
    router.go(-1)
  } else {
    router.push({
      name: 'Welcome'
    })
  }
}
let queryParse = (str) => {
  if (!str || str === '0') {
    return {}
  }
  let dataArr = decodeURIComponent(str).split('&')
  let params = {}
  dataArr.forEach(query => {
    let queryItem = query.split('=')
    if (queryItem.length === 1) {
      params.id = queryItem[0]
    } else {
      params[queryItem[0]] = queryItem[1]
    }
  })
  return params
}

let href = window.location.href.replace(/#/g, '').replace(/\?utm_source=tokenpocket/g, '')
if (href.includes('?')) {
  let search = href.slice(href.indexOf('?') + 1, href.length)
  let params = queryParse(search)
  if (params.intro) {
    // 是否有推荐邀请码
    intro.set(params.intro)
  }
  if(params.type) {
    region.set(params.type)
  }
}

const app = createApp(App);
app.config.globalProperties.$goBack = goBack;
app.config.globalProperties.$dateformat = dateformat;
app.config.globalProperties.$setErrorImg = (e) => {
  e.target.src = require('@/assets/images/not_found.png')
};
app.component('ThemeButton1', ThemeButton1);
app.component('ThemeButton2', ThemeButton2);
app
  .use(router)
  .use(store)
  .use(i18n)
  .use(Vant)
  .mount('#app');
